html {
  font-size: 62.5%;
  * {
    box-sizing: border-box; } }

body {
  @include resetmargin();
  position: relative;
  font-family: "Lato", sans-serif; }

.btn-custom {
  padding: 15px 5px 17px;
  width: 100%;
  @include text(1.4rem, initial, $white);
  line-height: 1.8rem;
  letter-spacing: -0.3px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  background-color: $ui;
  border-color: $ui;
  border-radius: 35px;
  transition: background-color $transition;
  @include media-breakpoint-up(md) {
    padding: 15px 30px 17px;
    font-size: 1.5rem; }
  &:hover, &:focus {
    background-color: lighten($ui, 15%);
    border-color: $ui;
    color: $white;
    text-decoration: none; }
  &:not(:disabled):not(.disabled):active {
    background-color: lighten($ui, 25%);
    border-color: $ui; }
  &--sign-up {
    padding: 21px 34px 21px;
    letter-spacing: 0.3px; } }


.input-custom {
  padding: 15px 40px 15px 23px;
  @include text(1.2rem, initial, $black);
  text-transform: uppercase;
  line-height: 1.8rem;
  border-radius: 35px;
  @include media-breakpoint-up(md) {
    font-size: 1.5rem; } }


.common-title {
  @include text(3rem, 600, $black);
  line-height: 3.6rem;
  text-transform: uppercase;
  &--highlight {
    color: $main-color; }
  &--white {
    color: $white; }
  &--lowercase {
    text-transform: none; } }

.common-text {
  @include text(1.8rem, initial, $black);
  line-height: 22px;
  &--highlight {
    font-weight: 600; }
  &--white {
    color: $white; } }



